import { NavCommon } from "@/mixins/";

let mainWallet = "bnb19h0lsxhd92ar0d8uvxeuyx83de6ms0ry8jpc9v";

if (process.env["NODE_ENV"] === "development") {
  mainWallet = "bnb19h0lsxhd92ar0d8uvxeuyx83de6ms0ry8jpc9v";
}

export default {
  namespaced: true,
  state: {
    loading: true,
    withdrawalsList: [],
    withdrawalShow: {},
    pageCount: 1,
    blockioSecret: "",
    blockioApi: "",
    btcLastPrice: 0,
    totalBTC: 0,
    totalUSD: 0,
    massivePaymentsModalEnabled: false,
    manualPaymentModalEnabled: false,
    detailModalOpen: false,
    withdrawalSelectedItems: [],
    totalMassivePaymentFees: 0,
    mainWallet: "",
    totalPaymentBTC: 0,
    massivePaymentCache: [],
    spareMassivePaymentFees: 0,
    pageNum: 1,
    perPage: 25,
  },

  getters: {
    isLoading: (state) => state.loading,
    getWithdrawalsList: (state) => state.withdrawalsList,
    getWithdrawalShow: (state) => state.withdrawalShow,
    getPageCount: (state) => state.pageCount,
    getMassivePaymentModalEnabled: (state) => state.massivePaymentsModalEnabled,
    getManualPaymentModalOpen: (state) => state.manualPaymentModalEnabled,
    getDetailModalOpen: (state) => state.detailModalOpen,
    getBtcLastPrice: (state) => state.btcLastPrice,
    getBlockioApi: (state) => state.blockioApi,
    getBlockioSecret: (state) => state.blockioSecret,
    totalBTC: (state) => state.totalBTC,
    totalUSD: (state) => state.totalUSD,
    getWithdrawalSelectedItems: (state) => state.withdrawalSelectedItems,
    getTotalMassivePaymentFees: (state) => state.totalMassivePaymentFees,
    getMainWallet: (state) => state.mainWallet,
    getTotalPaymentBTC: (state) => state.totalPaymentBTC,
    getMassivePaymentCache: (state) => state.massivePaymentCache,
    getSpareMassivePaymentFees: (state) => state.spareMassivePaymentFees,
    getPageNum: (state) => state.pageNum,
    getPerPage: (state) => state.perPage,
  },

  mutations: {
    setLoading(state, isLoading) {
      state.loading = isLoading;
    },
    setWithdrawalsList(state, data) {
      state.withdrawalsList = data;
    },
    setWithdrawalsShow(state, data) {
      state.withdrawalShow = data;
    },
    setPageCount(state, data) {
      state.pageCount = data;
    },
    setBlockioApi(state, data) {
      state.blockioApi = data;
    },
    setBlockioSecret(state, data) {
      state.blockioSecret = data;
    },
    setBTCLastPrice(state, data) {
      state.btcLastPrice = data;
    },
    setTotalBTC(state, data) {
      state.totalBTC = data;
    },
    setTotalUSD(state, data) {
      state.totalUSD = data;
    },
    setMassivePaymentModalEnabled(state, data) {
      state.massivePaymentsModalEnabled = data;
    },
    setManualPaymentModalOpen(state, data) {
      state.manualPaymentModalEnabled = data;
    },
    setDetailModalOpen(state, data) {
      state.detailModalOpen = data;
    },
    setWithdrawalSelectedItems(state, item) {
      state.withdrawalSelectedItems.push(item);
    },
    resetWithdrawalSelectedItems(state) {
      state.withdrawalSelectedItems = [];
    },
    setTotalMassivePaymentFees(state, data) {
      state.totalMassivePaymentFees = data;
    },
    setMainWallet(state, data) {
      state.mainWallet = data;
    },
    setTotalPaymentBTC(state, data) {
      state.totalPaymentBTC = data;
    },
    setMassivePaymentCache(state, data) {
      state.massivePaymentCache = data;
    },
    setSpareMassivePaymentFees(state, data) {
      state.spareMassivePaymentFees = data;
    },
    setPageNum(state, data) {
      state.pageNum = data;
    },
    setPerPage(state, data) {
      state.perPage = data;
    },
  },

  actions: {
    /**
     * Fetch list of withdrawals
     *
     * @param state
     * @param commit
     * @param data
     * @returns {Promise<[]>}
     */
    async fetchSummary({ dispatch, commit, state, rootGetters }, data) {
      let withdrawalStatus = data.withdrawalStatus
        ? `&status=${data.withdrawalStatus}`
        : ``;

      commit("setLoading", true);
      try {
        let response = await this._vm.$http({
          url: "/api/v1/admin/stats/withdrawals?" + withdrawalStatus,
          headers: {
            "content-type": "application/json",
            Authorization: rootGetters.authToken,
          },
        });
        dispatch("calcTotalCrypto", response.data);
        commit("setLoading", false);
      } catch (error) {
        commit("setLoading", false);
        throw error;
      }
      return state.withdrawalsList;
    },

    /**
     * Fetch list of withdrawals
     *
     * @param state
     * @param commit
     * @param data
     * @returns {Promise<[]>}
     */
    async fetchList({ state, commit, getters, rootGetters }, data) {
      let username = data.username ? `&username=${data.username}` : ``;
      let pageNum = `&page=${getters.getPageNum}`;
      let perPage = `&per_page=${getters.getPerPage}`;

      // let withdrawalType = data.withdrawalType
      //   ? `&category=${data.withdrawalType}`
      //   : ``;
      let withdrawalStatus = data.withdrawalStatus
        ? `&status=${data.withdrawalStatus}`
        : ``;

      commit("setLoading", true);
      try {
        let response = await this._vm.$http({
          url:
            "/api/v1/admin/withdrawals?" +
            withdrawalStatus +
            perPage +
            pageNum +
            username,
          headers: {
            "content-type": "application/json",
            Authorization: rootGetters.authToken,
          },
        });
        commit("setWithdrawalsList", response.data);
        commit("setLoading", false);
        commit(
          "setPageCount",
          Math.ceil(
            parseFloat(
              response.headers["per-page-total"] /
                parseInt(response.headers["per-page"])
            )
          )
        );
      } catch (error) {
        commit("setLoading", false);
        throw error;
      }
      return state.withdrawalsList;
    },

    /**
     * Fetch individual withdrawal
     *
     * @param state
     * @param commit
     * @param uuid
     * @returns {Promise<[]>}
     */
    selectedWithdrawal({ commit }, data) {
      commit("setManualPaymentModalOpen", true);
      commit("setWithdrawalsShow", data);
    },

    /**
     * Update withdrawal
     *
     * @param commit
     * @param data
     * @returns {Promise<[]>}
     */
    async updateWithdrawal({ commit, rootGetters, getters, dispatch }, data) {
      commit("setLoading", true);
      try {
        let response = await this._vm.$http({
          url: "/api/v1/admin/withdrawals/" + getters.getWithdrawalShow.uuid,
          method: "PATCH",
          headers: {
            "content-type": "application/json",
            Authorization: rootGetters.authToken,
          },
          data: {
            withdrawal_status: "paid",
            txid: getters.getWithdrawalShow.txid,
          },
        });

        this._vm.$toasted.show("Payment Sent!", {
          position: "top-right",
          duration: 5000,
          type: "success",
          closeOnSwipe: true,
        });
        dispatch("fetchList", {
          withdrawalStatus: "pending",
        });
        commit("setLoading", false);
        commit("setManualPaymentModalOpen", false);
        commit("setWithdrawalsShow", {});
      } catch (error) {
        commit("setLoading", false);
        error.response.data.message.map((m) => {
          this._vm.$toasted.show(m, {
            position: "top-right",
            duration: 5000,
            type: "default",
            closeOnSwipe: true,
          });
        });
      }
    },

    /**
     * Delete  withdrawal
     *
     * @param commit
     * @param data
     * @returns {Promise<[]>}
     */
    async deleteWithdrawal({ commit, rootGetters, dispatch }, data) {
      if (NavCommon["methods"].validateDelete()) {
        commit("setLoading", true);
        try {
          let response = await this._vm.$http({
            url: "/api/v1/admin/withdrawals/" + data.uuid,
            method: "DELETE",
            headers: {
              "content-type": "application/json",
              Authorization: rootGetters.authToken,
            },
          });

          this._vm.$toasted.show("Withdrawal Successfully Destroyed", {
            position: "top-right",
            duration: 5000,
            type: "danger",
            closeOnSwipe: true,
          });
          commit("setLoading", false);
          commit("resetWithdrawalSelectedItems");
          dispatch("fetchList", {
            withdrawalStatus: "pending",
          });
        } catch (error) {
          error.response.data.map((m) => {
            this._vm.$toasted.show(m, {
              position: "top-right",
              duration: 5000,
              type: "default",
              closeOnSwipe: true,
            });
          });
          commit("setLoading", false);
        }
      }
    },

    /**
     * Calculate amount due in BTC and USD
     *
     * @param commit
     * @param rootGetters
     * @param data
     */
    async calcTotalCrypto({ commit, rootGetters }, data) {
      // let totalUSD = data.total;
      // // commit("setTotalBTC", totalBTC.toFixed(6));
      // commit("setTotalUSD", totalUSD.toFixed(6));

      let totalUSD = data.total;
      commit("setLoading", true);
      try {
        let response = await this._vm.$http({
          url:
            "/api/v1/admin/blockio_wallets/get_current_price/?currency_id=14&price_base=USD",
          method: "GET",
          headers: {
            "content-type": "application/json",
            Authorization: rootGetters.authToken,
          },
        });
        let prices =
          (await response.data.data.prices) &&
          response.data.data.prices.map((x) => parseFloat(x.price));
        let lastPrice = await Math.max.apply(null, prices);
        let totalBTC = await (totalUSD / lastPrice);
        commit("setBTCLastPrice", lastPrice);
        commit("setTotalBTC", totalBTC.toFixed(6));
        commit("setTotalUSD", totalUSD.toFixed(2));
        commit("setLoading", false);
      } catch (error) {
        commit("setLoading", false);
        throw error;
      }
    },

    /**
     * Calculate network fees for payment
     *
     * @param commit
     * @param rootGetters
     * @param data
     */
    async buildPaymentFees({ commit, rootGetters, getters }, data) {
      commit("setMainWallet", mainWallet);
      commit("setLoading", true);
      let values = data.items.map((item) => item.totalCrypto);
      let wallets = data.items.map((item) => item.wallet);

      try {
        let response = await this._vm.$http(
          "/api/v1/admin/blockio_wallets/get_network_fee_estimate?api_key=" +
            getters["getBlockioApi"] +
            "&amounts=" +
            values.toString() +
            "&to_addresses=" +
            wallets.toString() +
            "&pin=" +
            getters["getBlockioSecret"],
          {
            headers: {
              "content-type": "application/json",
              Authorization: rootGetters.authToken,
            },
          }
        );

        let networkFee = parseFloat(response.data.data.estimated_network_fee);
        let secondNetworkFee = parseFloat(response.data.data.black_box_way_fee);
        let totalAmount = Number(values.reduce((a, b) => a + b, 0).toFixed(6));
        let totalFees = secondNetworkFee + networkFee;
        let spareFees = secondNetworkFee;
        let totalPayment = totalAmount.toFixed(6);

        commit("setSpareMassivePaymentFees", spareFees);
        commit("setTotalMassivePaymentFees", totalFees);
        commit("setTotalPaymentBTC", totalPayment);
        commit("setLoading", false);
      } catch (error) {
        this._vm.$toasted.show("Some Error Ocurred", {
          position: "top-right",
          duration: 5000,
          type: "default",
          closeOnSwipe: true,
        });
        commit("setTotalPaymentBTC", 0);
        commit("setTotalMassivePaymentFees", 0);
        commit("setLoading", false);
      }
    },

    /**
     * Send massive payment request
     *
     * @param commit
     * @param rootGetters
     * @param data
     */
    async createMassivePayment({ commit, getters, dispatch }, data) {
      commit("setLoading", true);
      let values = await data.items.map((item) => item.totalCrypto);
      let wallets = await data.items.map((item) => item.wallet);
      let itemsIDS = await data.items.map((item) => item.id);

      // set second network fees
      // values.push(0.00001);
      values.push(getters.getSpareMassivePaymentFees);
      wallets.push(getters.getMainWallet);

      for (let i = 0; i < wallets.length; i++) {
        await setTimeout(() => {
          let object = {
            values: values[i],
            wallets: wallets[i],
            itemsIDS: itemsIDS[i],
          };
          dispatch("massivePaymentCall", object);
          if (i + 1 === wallets.length) {
            commit("setLoading", false);
            commit("setMassivePaymentModalEnabled", false);
            commit("resetWithdrawalSelectedItems");
            commit("setMassivePaymentCache", []);
            dispatch("fetchList", {
              withdrawalStatus: "pending",
            });
          }
        }, 3000 * i);
      }
    },

    /**
     * Send massive payment api call to server
     *
     * @param commit
     * @param rootGetters
     * @param data
     */
    async massivePaymentCall({ rootGetters, commit, getters }, data) {
      try {
        const response = await this._vm.$http({
          url:
            "/api/v1/admin/blockio_wallets/withdraw/?api_key=" +
            getters["getBlockioApi"] +
            "&amounts=" +
            data.values.toString() +
            "&to_addresses=" +
            data.wallets.toString() +
            "&priority=custom" +
            "&custom_network_fee=" +
            getters.getTotalMassivePaymentFees +
            "&pin=" +
            getters["getBlockioSecret"] +
            "&itemsIDS=" +
            data.itemsIDS,
          method: "GET",
          headers: {
            "content-type": "application/json",
            Authorization: rootGetters.authToken,
          },
        });
        this._vm.$toasted.show("Massive Payment Sent!", {
          position: "top-right",
          duration: 5000,
          type: "success",
          closeOnSwipe: true,
        });
      } catch (error) {
        commit("setLoading", false);
        this._vm.$toasted.show("Some Error Ocurred", {
          position: "top-right",
          duration: 5000,
          type: "default",
          closeOnSwipe: true,
        });
      }
    },

    /**
     * Reset state to initial values
     *
     * @param commit
     */
    reset({ commit }) {
      commit("setWithdrawalsList", []);
      commit("setWithdrawalsShow", {});
      commit("setLoading", false);
      commit("setBlockioApi", "");
      commit("setBlockioSecret", "");
      commit("setBTCLastPrice", 0);
      commit("setTotalBTC", 0);
      commit("setTotalUSD", 0);
      commit("setMassivePaymentModalEnabled", false);
      commit("setManualPaymentModalOpen", false);
      commit("setDetailModalOpen", false);
      commit("resetWithdrawalSelectedItems");
      commit("setMassivePaymentCache", []);
    },
  },
};
