import Vue from "vue";
import Vuex from "vuex";
import VuexPersist from "vuex-persist";

import { HTTP } from "@/utils/http-common";
import { subscriptions, withdrawals } from "./api";

Vue.use(Vuex);

const vuexLocalStorage = new VuexPersist({
  key: "vuex", // The key to store the state on in the storage provider.
  storage: window.localStorage, // or window.sessionStorage or localForage
  // Function that passes the state and returns the state with only the objects you want to store.
  // reducer: state => state,
  // Function that passes a mutation and lets you decide if it should update the state in localStorage.
  // filter: mutation => (true)
});

export const store = new Vuex.Store({
  plugins: [vuexLocalStorage.plugin],
  modules: { subscriptions, withdrawals },
  state: {
    currentUser: {},
    authToken: "",
    activeSession: false,
    usersSelectList: [],
  },
  mutations: {
    setCurrentUser(state, user) {
      state.currentUser = user;
    },
    setAuthToken(state, token) {
      state.authToken = token;
    },
    setActiveSession(state, status) {
      state.activeSession = status;
    },
    setUsersSelectList(state, items) {
      state.usersSelectList = items;
    },
  },
  getters: {
    currentUser: (state) => state.currentUser,
    authToken: (state) => state.authToken,
    activeSession: (state) => state.activeSession,
    usersSelectList: (state) => state.usersSelectList,
  },
  actions: {
    fetchUsersSelectData(context) {
      HTTP(`/api/v1/admin/users/usernames`, {
        headers: {
          "content-type": "application/json",
          Authorization: store.getters.authToken,
        },
      })
        .then((response) => {
          context.commit("setUsersSelectList", response.data);
        })
        .catch((error) => {});
    },
  },
});
