'use strict';

export const NavCommon = {
  methods: {
    async logout() {
      await localStorage.removeItem("auth_token");
      await localStorage.removeItem("vuex");
      await window.location.replace("#/login");
    },

    validateDelete() {
      const answer = window.confirm('Are you sure you want to destroy this item?')
      if (answer) return true
    }
  }
}