'use strict';

export const AuthCommon = {
  methods: {
    async checkSession() {
      try {
        const res = await this.$http({
          url: '/api/v1/client/sessions/user',
          method: "get",
          headers: {
            "content-type": "application/json",
            Authorization: this.$store.getters.authToken
          }
        })
        const data  = await res.data;
        const update = await this.$store.commit("setActiveSession", true);
      } catch (e) {
        this.$store.commit("setActiveSession", false);
        this.$router.push("/login");
      }
    }
  }
}