export default {
  namespaced: true,
  state: {
    loading: true,
    subscriptionsList: [],
    subscriptionsShow: {},
    pageCount: 1,
  },

  getters: {
    isLoading(state) {
      return state.loading;
    },
    getSubscriptionsList(state) {
      return state.subscriptionsList;
    },
    getSubscriptionsShow(state) {
      return state.subscriptionsShow;
    },
    getPageCount(state) {
      return state.pageCount;
    },
  },

  mutations: {
    setLoading(state, isLoading) {
      state.loading = isLoading;
    },
    setSubscriptionsList(state, data) {
      state.subscriptionsList = data;
    },
    setSubscriptionsShow(state, data) {
      state.subscriptionsShow = data;
    },
    setPageCount(state, data) {
      state.pageCount = data;
    },
  },

  actions: {
    /**
     * Fetch array of subscriptions
     *
     * @param state
     * @param commit
     * @param data
     * @returns {Promise<[]>}
     */
    async fetchList({ state, commit, rootGetters }, data) {
      let username = data.username ? `&username=${data.username}` : ``;
      let pageNum = data.pageNum ? `&page=${data.pageNum}` : ``;
      let perPage = data.perPage ? `&per_page=${data.perPage}` : ``;
      let subscriptionStatus = data.subscriptionStatus
        ? `&status=${data.subscriptionStatus}`
        : ``;
      let suscriptionType = data.suscriptionType
        ? `&category=${data.suscriptionType}`
        : ``;

      commit("setLoading", true);
      try {
        let response = await this._vm.$http({
          url:
            "/api/v1/admin/subscriptions?" +
            perPage +
            pageNum +
            username +
            subscriptionStatus +
            suscriptionType,
          headers: {
            "content-type": "application/json",
            Authorization: rootGetters.authToken,
          },
        });
        commit("setSubscriptionsList", response.data);
        commit("setLoading", false);
        commit(
          "setPageCount",
          Math.ceil(
            parseFloat(
              response.headers["per-page-total"] /
                parseInt(response.headers["per-page"])
            )
          )
        );
      } catch (error) {
        commit("setLoading", false);
        throw error;
      }
      return state.subscriptionsList;
    },

    /**
     * Fetch unique subscription
     *
     * @param state
     * @param commit
     * @param uuid
     * @returns {Promise<[]>}
     */
    async fetchSubscription({ state, commit }, uuid) {
      commit("setLoading", true);
      try {
        let response = await this._vm.$http({
          url: `/api/v1/client/subscriptions?per_page=100&page=1/${uuid}`,
        });
        commit("setSubscriptionsShow", response.data);
        commit("setLoading", false);
      } catch (error) {
        commit("setLoading", false);
        throw error;
      }
      return state.subscriptionsShow;
    },

    /**
     * Reset state to initial values
     *
     * @param commit
     */
    reset({ commit }) {
      commit("setSubscriptionsList", []);
      commit("setSubscriptionsShow", {});
      commit("setLoading", true);
    },
  },
};
